import axios from 'axios';

import React, { Component } from 'react';
import { connect } from 'react-redux';

import endpoints from '../../redux/api/endpoints';
import { createUrlParamsFromFilters } from '../../redux/api/utils';
import { replaceItemInDictArrayById, getObjectbyId } from '../../components/utils';


export class QuotaOverview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      total_volume_quota_brus: null,
      total_volume_quota_doska: null,
      total_volume_sold_brus: null,
      total_volume_sold_doska: null,
      brus_balance: null,
      doska_balance: null,

      message: null,
      error: null,
    }
    // this.changeMarketCost = this.changeMarketCost.bind(this);
  }

  componentDidMount() {
    const token = localStorage.getItem('token');
    const params = createUrlParamsFromFilters({rama: this.props.user.rama_id});
    axios({
      method: 'get',
      url: endpoints.MANAGER_QUOTA_OVERVIEW,
      headers: {'Authorization': `JWT ${token}` },
      params: params
    })
    .then(res => {
      this.setState({ 
        total_volume_quota_brus: res.data.total_volume_quota_brus,
        total_volume_quota_doska: res.data.total_volume_quota_doska,
        total_volume_sold_brus: res.data.total_volume_sold_brus,
        total_volume_sold_doska: res.data.total_volume_sold_doska,
        brus_balance: res.data.brus_balance,
        doska_balance: res.data.doska_balance,
      });
    })
  }

  render() {
    const { } = this.state
    let brusBalance = 
      (this.state.total_volume_quota_brus - this.state.total_volume_sold_brus).toFixed()
    return (
      <div className='card card-style my-2'>
        <div className='content'>
          <h4 className='mb-2'>План пилорамы</h4>
          <table className='table table-sm'>
            <thead>
              <th>-</th>
              <th>План</th>
              <th>Отгружено</th>
              <th>Баланс</th>
            </thead>
            <tbody>
              <tr>
                <td>Брус</td>
                <td>{this.state.total_volume_quota_brus}</td>
                <td>{this.state.total_volume_sold_brus}</td>
                <td>{this.state.brus_balance && this.state.brus_balance.toFixed(4)}
                </td>
              </tr>
              <tr>
                <td>Доска</td>
                <td>{this.state.total_volume_quota_doska}</td>
                <td>{this.state.total_volume_sold_doska}</td>
                <td>{this.state.doska_balance && this.state.doska_balance.toFixed(4)}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    )
  }
}


const mapStateToProps = (state) => ({
  user: state.auth.user
})

const mapDispatchToProps = (dispatch) => ({
  dispatch: dispatch,
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(QuotaOverview);